@import url(//fonts.googleapis.com/css?family=Open+Sans:400italic,600italic,600,400,300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Chart {
  position: relative;
  flex-grow: 1;
}

.Chart-canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.Chart-canvas-crosshair {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.ChartSidebar {
  padding: 15px;
  width: 20%;
}

@media (max-width: 800px) {
  .ChartSidebar  {
    width: 100%;
  }
}

.ChartSidebar-dark {
  background: #000;
  color: #aaa;
}

.ChartSidebar-control {
  margin-bottom: 10px;
}

.ChartSidebar-control label {
  font-size: 16px;
  font-weight: lighter;
  display: block;
}

.ChartSidebar-control input, .ChartSidebar-control select {
  display: block;
  padding: 2px 8px;
  font-size: 14px;
}

.ChartSidebar-control-dark input, .ChartSidebar-control-dark select {
  color: #ddd;
  background: #000;
  border: 1px solid #444;
}

.ChartSidebar-price {
  margin: 10px 0;
}

.ChartSidebar-change {
  font-size: 30px;
}

.ChartSidebar-percent {
  font-size: 30px;
  font-weight: bold;
}

.ChartSidebar-bull {
  color: #008800;
}

.ChartSidebar-bear {
  color: #dd0000;
}

.ChartSidebar-table {
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 16px;
}

.ChartSidebar-table th, .ChartSidebar-table td {
  padding: 4px 10px;
}

.ChartSidebar-table-dark th, .ChartSidebar-table-dark td {
  border-bottom: 1px solid #444;
}

.ChartSidebar-table-light th, .ChartSidebar-table-light td {
  border-bottom: 1px solid #eee;
}

.ChartSidebar-table th {
  color: #888;
  text-align: left;
  font-weight: normal;
  padding-left: 0;
}

.ChartSidebar-gihub {
  display: flex;
  flex-direction: column;
}

.ChartSidebar-gihub svg {
  margin-right: 5px;
}

.ChartSidebar-gihub a {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #666;
  text-decoration: none;
  margin-bottom: 5px;
}

.ChartView-chart-container {
  flex-grow: 1;
  display: flex;
}

.App {
  display: flex;
  flex-direction: column;
  font-family: "Open Sans";
  min-height: 100vh;
}

.App-dark {
  background: #000;
}

.App-header {
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 0;
  padding: 10px 15px;
  height: 26px;
  z-index: 10;
  font-weight: lighter;
  font-size: 22px;
}

.App-header-light {
  background: #fff;
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.05);
  border-bottom: 1px solid #f4f4f4;
}

.App-header-dark {
  background: #000;
  color: #aaa;
  box-shadow: 0px 0px 20px 0px rgba(255,255,255,0.05);
  border-bottom: 1px solid #222;
}

.App-content {
  padding: 0;
  flex-grow: 1;
  display: flex;
  flex-direction: row;
}

@media (max-width: 800px) {
  .App-content {
    flex-direction: column;
  }
}

